.start-panel {
  text-align: center;
  background-color: rgb(250, 152, 82);
  color: black;
  border: 2px solid black;
  width: 40vw;
  height: 70vh;
}

.start-panel input {
  font-size: 1em;
  text-align: right;
  width: 2em;
}

.start-panel button {
  font-size: 1em;
}
@media screen and (max-width: 800px) {
  .start-panel {
    height: 100vw;
    width: 60vw;
  }
}
